import { Attribute, Directive, forwardRef } from '@angular/core';
import type { Validator } from '@angular/forms';
import { NG_VALIDATORS } from '@angular/forms';

// eslint-disable-next-line no-unused-vars
export const requiredValidatorLogic = (_required: boolean) => (control: { value: { id: null } }) => {
  if (!control.value) {
    return { valid: false };
  }

  return control.value.id && control.value.id !== null ? null : { valid: false };
};

const Required_VALIDATOR: any = {
  provide: NG_VALIDATORS,
  // eslint-disable-next-line @typescript-eslint/no-use-before-define
  useExisting: forwardRef(() => RequiredValidator),
  multi: true,
};

@Directive({
  selector: '[appIsRequired]',
  providers: [Required_VALIDATOR],
})
export class RequiredValidator implements Validator {
  private _validator: any;

  constructor(@Attribute('isRequired') required: boolean) {
    this._validator = requiredValidatorLogic(required);
  }

  validate(c: any) {
    return this._validator(c);
  }
}
