/* eslint-disable @typescript-eslint/consistent-type-imports */
/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { Directive, ElementRef, Input, OnDestroy, OnInit, Renderer2 } from '@angular/core';
import { FormGroup, FormGroupDirective } from '@angular/forms';
import type { Observable } from 'rxjs';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import type { IFormErrors } from './error-message.interface';

@Directive({
  selector: '[appValidationError]',
})
export class ValidationErrorDirective implements OnInit, OnDestroy {
  /**
   *
   * @usage add ValidationError [showError$]="your subject variable"
      [errorMessages]="your error list variable" to your form element
   */
  constructor(private el: ElementRef, private renderer: Renderer2, private form: FormGroupDirective) {}
  @Input()
  showError$!: Observable<boolean>;
  @Input()
  errorMessages!: IFormErrors[];
  onDestroy$ = new Subject<void>();

  ngOnDestroy(): void {
    this.onDestroy$.next();
  }

  ngOnInit(): void {
    this.clearAllErrors();

    this.showError$.pipe(takeUntil(this.onDestroy$)).subscribe(() => {
      this.checkAllInputs();
    });
    this.onInputClearError();
  }

  onInputClearError() {
    // on any input value change clear all errors
    this.formGroup.valueChanges.pipe(takeUntil(this.onDestroy$)).subscribe(() => {
      this.clearAllErrors();
    });
  }

  get allInputChildren(): NodeListOf<HTMLInputElement> {
    return (this.el.nativeElement as HTMLElement).querySelectorAll(
      'input,kendo-dropdownlist,kendo-colorpicker,kendo-fileselect,kendo-numerictextbox,p-colorpicker,kendo-datepicker,p-inputNumber,p-dropdown,kendo-timepicker'
    );
  }

  /**Returns FormGroup of the form */
  get formGroup(): FormGroup {
    return this.form.control;
  }

  /**Returns FormControl name by index of the list */
  getTargetInputFormControlName(index: number) {
    return this.allInputChildren[index].attributes.getNamedItem('formcontrolname')?.nodeValue;
  }

  checkAllInputs(): void {
    this.allInputChildren.forEach((input, index) => {
      const formControlName = this.getTargetInputFormControlName(index);
      const valid = this.getFormControlValidStatus(formControlName!);
      if (!valid) {
        const parentDiv = input.parentElement;
        this.addErrorStyle(parentDiv);
        this.addErrorMessage(parentDiv!, input, formControlName!);
      }
    });
  }

  clearAllErrors(): void {
    this.allInputChildren.forEach((input) => {
      const parentDiv = input.parentElement;
      this.removeErrorMessage(parentDiv!, input);
      this.removeErrorStyle(parentDiv!);
    });
  }

  getFormControlValidStatus(controlName: string): boolean {
    const formControl = this.formGroup.get(controlName);
    return formControl ? formControl.valid : true;
  }

  addErrorStyle(parent: HTMLElement | null): void {
    this.renderer.addClass(parent, 'message-wrp');
    this.renderer.addClass(parent, 'error');
  }

  removeErrorStyle(parent: HTMLElement): void {
    this.renderer.removeClass(parent, 'message-wrp');
    this.renderer.removeClass(parent, 'error');
  }

  getErrorText(controlName: string): string {
    const error = Object.keys(this.formGroup.get(controlName)!.errors!)[0];
    const formErrors = this.errorMessages.find((e) => e.controlName === controlName);
    const message = formErrors!.errors.find((e) => e.error === error)!.message;
    return message ?? `please enter a valid ${error}`;
  }

  getErrorMessage(controlName: string) {
    const span = this.renderer.createElement('span');
    const messageText = this.getErrorText(controlName);
    const message = this.renderer.createText(messageText);
    this.renderer.addClass(span, 'message');
    this.renderer.addClass(span, 'text-danger');
    this.renderer.addClass(span, 'text-left');
    this.renderer.addClass(span, 'd-block');
    this.renderer.appendChild(span, message);
    return span;
  }

  addErrorMessage(parent: HTMLElement, input: HTMLInputElement, controlName: string): void {
    const exists = this.renderer.nextSibling(input);
    if (!exists) {
      const messageSpan = this.getErrorMessage(controlName);
      this.renderer.appendChild(parent, messageSpan);
    }
  }

  removeErrorMessage(parent: HTMLElement, input: HTMLInputElement): void {
    const exsistingSpan = this.renderer.nextSibling(input);
    exsistingSpan && this.renderer.removeChild(parent, exsistingSpan);
  }
}
